      <h2 id="gehaltstarifvertrag" class="mb-4 mt-5">
        <span class="display-4 bold">Gehaltstarifvertrag MFA 2023</span>
        <br />
        fr Medizinische Fachangestellte / ArzthelferIn
      </h2>
      <div class="row">
        <div class="col">
          <p>
            Der Verband medizinischer Fachberufe e.V. (VMF) ist die
            verantwortliche Organisation fr Medizinische Fachangestellte auf
            Arbeitnehmerseite. Sie vertritt uns MFA bei Tarifverhandlungen
            gegenber der Arbeitsgemeinschaft zur Regelung der
            Arbeitsbedingungen der ArzthelferInnen / Medizinischen
            Fachangestellten (AAA). Die AAA ist die Tarifpartei der Arbeitgeber,
            sprich der niedergelassenen rzte, mit der der Manteltarifvertrag
            verhandelt wird.
          </p>
          <p>
            Die niedergelassenen rzte sind nicht automatisch Mitglied in der
            AAA. Deshalb kann man nicht zwingend davon ausgehen, dass sich
            Praxen an die Gehlter im Tarifvertrag halten mssen. Arbeitgeber
            sind nur dann dazu verpflichtet nach Gehaltstarifvertrag zu zahlen,
            wenn sowohl sie Mitglied im AAA als auch die MFA Mitglied im VMF
            sind.
          </p>
          <p>
            Ehrlich gesagt, kenne ich nicht viele ArzthelferInnen, die Mitglied
            im VMF sind. Daher wundert es auch nicht, dass so viele von uns
            untertariflich verdienen, wie die Kollegin aus dem Forum im obigen
            Beispiel.
          </p>
          <p>
            Dennoch gibt es viele rzte, die sich freiwillig an den aktuellen
            Tarifvertrag halten oder zumindest das Gehalt fr ihre Medizinischen
            Fachangestellten daran anlehnen.
          </p>
          <p>
            Neben dem offiziellen Tarifvertrag existieren zahlreiche
            Gehaltstarifvertrge von privaten Unternehmen, Krankenhusern, dem
            ffentlichen Dienst (z.B. wenn Du bei der Kassenrztlichen
            Vereinigung arbeitest) und kirchlichen Trgern (wie der Caritas, der
            Diakonie oder der Johanniter).
          </p>
          <p>
            Es kommt im privaten Bereich oftmals zu einer auertariflichen
            Bezahlung. Das muss nicht unbedingt negativ fr Dich ausfallen.
            Viele MFA werden dadurch bertariflich bezahlt.
          </p>
          <p>
            Wenn dein Chef kein Mitglied im AAA ist, Du aber darauf bestehst,
            dass dein Gehalt nach Tarifvertrag bezahlt wird, kannst du
            alternativ mit deinem Arbeitgeber vereinbaren, dass er sich
            freiwillig daran hlt. Hier ist es ratsam, eine entsprechende
            Klausel im Arbeitsvertrag aufnehmen zu lassen, die die Abmachung
            bindend macht.
          </p>
          <p>
            Der Tarifvertrag hat natrlich auch einen ganz entscheidenden
            Vorteil: er wird regelmig neu verhandelt. Dein Verdienst wird
            somit beinahe jhrlich immer etwas angepasst und erhht. Ist dein
            Chef verpflichtet nach Tarifvertrag zu zahlen oder hlt sich
            freiwillig daran, musst Du also selbst keine
            <b-link
              to="/blog/artikel/gehaltserhoehung-fuer-mfa-tipps-wie-du-erfolgreich-mehr-gehalt-verhandelst"
              >Gehaltsverhandlungen</b-link
            >
            fhren.
          </p>
          <p>
            Daneben bietet der Gehaltstarifvertrag fr Medizinische
            Fachangestellte Dir vor allem eine gute Orientierung, um deinen
            Marktwert einzuschtzen  bezglich Qualifikation und Erfahrung.
          </p>
