      </h3>
      <p>
        Ein wichtiger Indikator fr die Hhe des Verdienstes von MFA sind
        Erfahrung und Expertise, die man sich im Laufe seines Berufslebens
        aneignet. Je lnger Du im Beruf bist, desto mehr kannst du
        logischerweise an Gehalt verlangen. Auch in den Tarifverhandlungen
        werden Gehlter nach Berufsjahren und Ttigkeitsgruppen vereinbart.
      </p>
      <p>
        Schauen wir uns einmal an, wie Du deine Berufsjahre berechnen kannst.
      </p>
      <p>
        Die Anrechnung deiner Berufsjahre startet ab dem 1. des Monats, in dem
        die Ausbildung beendet wurde.
      </p>

      <div class="example">
        <p><strong>Beispiel:</strong></p>
        <p class="pl-5">
          <em>
            Ausbildung am 16.07. beendet -> Anrechnung der Berufsjahre beginnt
            ab 01.07.
          </em>
        </p>
      </div>
      <p>
        Bei Beschftigten in Teilzeit zhlen die Berufsjahre genau wie bei
        Vollzeit-Beschftigung und werden komplett angerechnet.
      </p>
      <h3 id="berufsnahe-taetigkeiten">
        Anrechnung der Berufsjahre fr MFA bei berufsnahen Ttigkeiten
      </h3>
      <p>
        Bist Du vor oder nach deiner Ausbildung einer berufsnahen Arbeit
        nachgegangen (z.B. in einem Labor oder im Krankenhaus), werden die
        Zeiten zur Hlfte angerechnet. Du musst allerdings deine Prfung zur
        Medizinischen Fachangestellten vor der rztekammer bestanden haben.
      </p>
      <div class="example">
        <p><strong>Beispiel:</strong></p>
        <p class="pl-5">
          <em>
            Eine Medizinische Fachangestellte arbeitet vor (oder nach) ihrer
            Ausbildung vier Jahre in einer Zahnarztpraxis. Hier werden 2
            Berufsjahre angerechnet.
          </em>
        </p>
      </div>
      <h3 id="auszeiten">
        Mutterschutz, Elternzeit und Erziehungsurlaub
      </h3>
      <p>
        Unterbrichst Du die Arbeit fr Mutterschutz, Elternzeit oder
        Erziehungsurlaub, knnen diese Zeiten nur angerechnet werden, wenn du
        Dich in einem aktiven Arbeitsverhltnis befindest.
      </p>
      <p>
        Zeiten fr Mutterschutz werden dann voll bei den Berufsjahren
        angerechnet.
      </p>
      <p>
        Zeiten fr Erziehungsurlaub bzw. Elternzeit werden nur zur Hlfte zu den
        Berufsjahren gezhlt. Arbeitest Du whrend der Elternzeit in Teilzeit
        als ArzthelferIn, werden die Zeiten jedoch voll dazu gerechnet.
      </p>
      <p>Zwei Beispiele zum besseren Verstndnis.</p>
      <div class="row my-3">
        <div class="col mx-lg-4">
          <div class="example">
            <p><strong>Beispiel 1:</strong></p>
            <p class="pl-5">
              <em>
                Eine Medizinische Fachangestellte geht am 15. August 2016 bei
                errechnetem Geburtstermin am 30.09.2016 (6 Wochen vor dem
                errechneten Entbindungstermin) bis zum 25.11.2016 (8 Wochen nach
                der Entbindung) in den Mutterschutz und nimmt eine einjhrige
                Elternzeit in Anspruch. Sie kehrt am 01.10.2017 in Vollzeit in
                die berufliche Ttigkeit als MFA zurck. Sie bekommt fr diese
                Zeit insgesamt 8,5 Monate bei den Berufsjahren angerechnet.
              </em>
            </p>
          </div>
        </div>
        <div class="col mx-lg-4">
          <div class="example">
            <p><strong>Beispiel 2:</strong></p>
            <p class="pl-5">
              <em>
                Eine Medizinische Fachangestellte geht am 15. August 2016 bei
                errechnetem Geburtstermin am 30.09.2016 in Mutterschutz bis zum
                25.11.2016 und nimmt anschlieend eine einjhrige Elternzeit in
                Anspruch. Ab dem 01.04.2017 arbeitet sie whrend der Elternzeit
                in Teilzeit 8 Stunden wchentlich in der Praxis. Sie kehrt am
                01.10.2017 aus der Elternzeit in die Praxis zurck. Sie bekommt
                fr diese Zeit insgesamt 11,5 Monate bei den Berufsjahren
                angerechnet.
              </em>
            </p>
          </div>
        </div>
      </div>

      <h3 id="taetigkeitsgruppen">
        Ttigkeitsgruppen nach aktuellem Gehaltstarifvertrag fr MFA
      </h3>
      <p>
        Im Gehaltstarifvertrag fr Medizinische Fachangestellte werden
        vorhandene Kenntnisse und Qualifikationen in Ttigkeitsgruppen
        eingeordnet und entsprechend im Gehalt widergespiegelt. Die Ttigkeiten
        werden nach Grad der Selbststndigkeit, Anzahl der Fortbildungsstunden,
        leitende Ttigkeiten und Verantwortung eingeteilt. Dies gibt Dir eine
        zustzliche Orientierung, um dein Gehaltsniveau einschtzen zu knnen.
        Hier steigern vor allem abgeschlossene
        <b-link to="/karriere/fort-und-weiterbildung"
          >Fortbildungen und Weiterbildungen</b-link
        >
        das Gehaltsniveau.
      </p>
      <p>
        Nach welchen Kriterien sich die Ttigkeitsgruppen aufteilen, zeigt dir
        die nachfolgende bersicht.
      </p>
      <div class="my-4">
        <div class="card mb-1 bg-light-shade">
          <h4 class="card-header h5 p-3">
            Ttigkeitsgruppe 1
          </h4>

          <div class="card-body pl-5">
            <ul>
              <li>
                Berufseinsteiger nach abgeschlossener Ausbildung zur
                medizinischen Fachangestellten
              </li>
              <li>
                Du fhrst Ttigkeiten nach Anweisungen des Arztes oder einer
                erfahreneren Kollegin aus
              </li>
            </ul>
          </div>
        </div>
        <div class="card mb-1 bg-light-shade">
          <h4 class="card-header h5 p-3">
            Ttigkeitsgruppe 2
          </h4>
          <div class="card-body pl-5">
            <ul>
              <li>
                Du kannst Ttigkeiten weitestgehend selbststndig ausfhren
              </li>
              <li>
                Du hast mind. 40 Fortbildungsstunden gesammelt und weist
                vertiefte Kenntnisse und Fertigkeiten in einem Arbeitsbereich
                auf
              </li>
            </ul>
            <p>
              <em>Fortbildungsmanahmen sind zum Beispiel:</em>
            </p>
            <ul>
              <li>
                Ambulante Versorgung lterer Menschen
              </li>
              <li>Wundbehandlung/ Wundmanagement</li>
              <li>Hygienemanagement</li>
              <li>Qualittsmanagement</li>
              <li>
                Patientenbegleitung und Koordination
              </li>
              <li>Datenschutz und Datensicherheit</li>
              <li>
                Informations- und Kommunikationstechnik
              </li>
              <li>
                Notfallmanagement/ Erweiterte Notfallkompetenz
              </li>
              <li>
                <b-link
                  to="/karriere/fort-und-weiterbildung/ueberblick/impfassistentin-fachkraft-fuer-impfmanagement"
                  >Impfassistenz</b-link
                >
              </li>
              <li>Disease-Management-Programme</li>
            </ul>
          </div>
        </div>
        <div class="card mb-1 bg-light-shade">
          <h4 class="card-header h5 p-3">
            Ttigkeitsgruppe 3
          </h4>
          <div class="card-body pl-5">
            <ul>
              <li>
                Du hast mindestens 80 Fortbildungsstunden gesammelt und/oder
                weist entsprechende Berufserfahrung nach
              </li>
              <li>
                Du fhrst Ttigkeiten in der Durchfhrung der Ausbildung der
                Medizinischen Fachangestellten aus
              </li>
            </ul>

            <p>
              <em>Fortbildungsmanahmen sind zum Beispiel:</em>
            </p>
            <ul>
              <li>
                Elektronische Praxiskommunikation und Telematik
              </li>
              <li>
                Prvention bei Jugendlichen und Erwachsenen
              </li>
              <li>
                Prvention im Kindes- und Jugendalter
              </li>
              <li>
                Strahlenschutzkurs lt.  24 (2) Rntgenverordnung (90 Stunden)
              </li>
            </ul>
          </div>
        </div>
        <div class="card mb-1 bg-light-shade">
          <h4 class="card-header h5 p-3">
            Ttigkeitsgruppe 4
          </h4>
          <div class="card-body pl-5">
            <ul>
              <li>
                Du fhrst Ttigkeiten komplett selbststndige aus
              </li>
              <li>
                Du hast besonders vielseitige Fachkenntnisse die durch Aneignung
                zustzlicher umfassender Fertigkeiten, Kenntnisse und
                Fhigkeiten in komplexen Arbeitsbereichen erworben
              </li>
              <li>
                Du hast mindestens 120 Fortbildungsstunden gesammelt und/oder
                bernimmst Ttigkeiten in der systematischen Planung,
                Durchfhrung und Koordination der Ausbildung der Medizinischen
                Fachangestellten
              </li>
            </ul>
            <p>
              <em>Fortbildungsmanahmen sind zum Beispiel:</em>
            </p>
            <ul>
              <li>
                <b-link
                  to="/karriere/fort-und-weiterbildung/ueberblick/ambulantes-operieren"
                  >Ambulantes Operieren</b-link
                >
              </li>
              <li>
                <b-link
                  to="/karriere/fort-und-weiterbildung/ueberblick/ambulantes-operieren-in-der-augenheilkunde"
                  >Ambulantes Operieren in der Augenheilkunde</b-link
                >
              </li>
              <li>
                Augenheilkundlich-technische Assistenz
              </li>
              <li>Dialyse</li>
              <li>Ernhrungsmedizin</li>
              <li>Gastroenterologische Endoskopie</li>
              <li>
                <b-link
                  to="/karriere/fort-und-weiterbildung/ueberblick/mfa-in-der-onkologie"
                  >Onkologie</b-link
                >
              </li>
              <li>Palliativversorgung</li>
              <li>Pneumologie</li>
              <li>
                Strahlenschutzkurs lt.  24 (2) Rntgenverordnung (120 Stunden)
              </li>
              <li>
                <b-link
                  to="/karriere/fort-und-weiterbildung/ueberblick/qualitaetsmanagementbeauftragte-r-qmb"
                >
                  Qualittsmanagement
                </b-link>
              </li>
              <li>Hygienemanagement</li>
              <li>
                <b-link
                  to="/karriere/fort-und-weiterbildung/ueberblick/naepa-und-eva"
                  >Nicht-rztliche Praxisassistenz</b-link
                >
              </li>
              <li>
                <b-link to="/karriere/fort-und-weiterbildung/ueberblick/verah">
                  Versorgungsassistentin in der Hausarztpraxis (VERAH)
                </b-link>
              </li>
            </ul>
          </div>
        </div>
        <div class="card mb-1 bg-light-shade">
          <h4 class="card-header h5 p-3">
            Ttigkeitsgruppe 5
          </h4>
          <div class="card-body pl-5">
            <ul>
              <li>
                Du fhrst leitungsbezogenen Ttigkeiten aus
              </li>
              <li>
                Du kannst Fortbildungsmanahmen von insgesamt mindestens 360
                Stunden und entsprechende Berufserfahrung vorweisen
              </li>
              <li>
                Hierbei sind eine Fortbildung von mindestens 120 Stunden und
                weitere Fortbildungseinheiten von mindestens 40 Stunden
                innerhalb eines Zeitraums von 5 Jahren zu erbringen. Dieser
                Zeitraum verlngert sich um die in Anspruch genommene
                Elternzeit.
              </li>
            </ul>

            <p>
              <em>Ein Beispiel fr eine solche Fortbildungsmanahme ist:</em>
            </p>
            <ul>
              <li>
                Fachwirtin fr ambulante Medizinische
                Versorgung/Arztfachhelferin gem  54 BBiG
              </li>
            </ul>
          </div>
        </div>
        <div class="card mb-1 bg-light-shade">
          <h4 class="card-header h5 p-3">
            Ttigkeitsgruppe 6
          </h4>
          <div class="card-body pl-5">
            <ul>
              <li>
                Du fhrst leitungs- und fhrungsbezogene Ttigkeiten aus
              </li>
              <li>
                Du kannst Fortbildungsmanahmen von insgesamt mindestens 600
                Stunden und entsprechende Berufserfahrung vorweisen
              </li>
            </ul>
            <p>
              <em>Ein Beispiel fr eine solche Fortbildungsmanahme ist:</em>
            </p>
            <ul>
              <li>
                Betriebswirtin fr Management im Gesundheitswesen gem  54
                BBiG
              </li>
              <li>
                <b-link
                  to="/karriere/fort-und-weiterbildung/ueberblick/fachwirtin-im-gesundheits-und-sozialwesen"
                  >Fachwirtin im Gesundheits- und Sozialwesen</b-link
                >
                gem  53 BBiG
              </li>
            </ul>
          </div>
        </div>
      </div>

